.grid {
  font-size: 0;
}

.grid > * {
  display: inline-block;
  width: 100%;
  margin-top: 0;
  font-size: medium;
  text-align: left;
  vertical-align: top;

  text-justify: auto;
}

.grid:after {
  display: inline-block;
  width: 100%;
  height: 0;
  content: "";
}



